import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
const root=ReactDOM.createRoot(document.getElementById("root"))


function HighInputs(props){
  return (
  <div className="high-inputs">
      <input type="time" placeholder="wake up time(8:30)" name='wakeUp' onChange={props.onChange}/>
      <select name="type" defaultValue="add mission" onChange={props.onChange}>
          <option value="" disabled>-- select type --</option>
          <option value="add mission">add mission</option>
          <option value="skip time">skip time</option>
      </select>
      <button className="reset"></button>
  </div>
  )
}


function LowInputs(props){
  return (
  <div className="inputs">
      <div className="duration">
          <p className="duration-label">duration:</p>
          <input type="number" id="duration" placeholder="minutes" onChange={props.onChange} name='duration'/>
      </div>
      <div className="mission">
          <p className="mission-label">mission:</p>
          <input type="text" id="mission" placeholder="what will you do?" onChange={props.onChange} name='mission' />
      </div>
      <button className='add'></button>
  </div>
  )
}


class App extends React.Component{
  constructor(props){
    super(props)
    this.state={
      wakeUp:"",
      type:"add mission",
      duration:"",
      mission:""
    }
    this.handleChange=this.handleChange.bind(this)
  }
  handleChange(ev){
    this.setState({[ev.target.name]:ev.target.value})
  }
  render(){
    return (
    <div className='input-div'>
      <h1>day planner</h1>
      <HighInputs onChange={this.handleChange}/>
      <LowInputs onChange={this.handleChange}/>
    </div>
    )
  }
}

root.render(<App/>);


